import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'lib-profilebutton',
  templateUrl: './profilebutton.component.html',
  styleUrls: ['./profilebutton.component.css']
})
export class ProfilebuttonComponent implements OnInit {

  constructor() { }

  ngOnInit(): void {
  }

  ProfileMenu() {
    let subMenu: any = document.getElementById("subMenu");
    subMenu.classList.toggle("open-menu");
  }
  OrgMenu() {
    let orgMenu: any = document.getElementById("orgMenu");
    orgMenu.classList.toggle("open-menu");
  }
  LangMenu() {
    let langMenu: any = document.getElementById("langMenu");
    langMenu.classList.toggle("open-menu");
  }

}
